* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

body {
    font-family: 'Roboto';
    font-weight: 400;
    color: grey;
    font-size: 16px;
    letter-spacing: 2px;
    padding: 50px;
    background-color: white; }

.container {
    background-color: #f7f7f7;
    height: 700px;
    width: 100%; }

.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90.05deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url(../img/header-background.jpg) center / cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        left: 50px;
        top: 50px;
        height: 70px;
        width: 56px; }
    &__box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }

.title-first {
    color: #FFFFFF;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 50px;
        line-height: 60px;
        letter-spacing: 10px;
        letter-spacing: 10px;
        margin-bottom: 10px;
        animation-name: LeftToRight;
        animation-delay: 0;
        animation-duration: 2s;
        animation-timing-function: ease; }
    &__secondary {
        display: block;
        font-style: normal;
        font-weight: 100;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: 5px;
        animation-name: RightToLeft;
        animation-delay: 0;
        animation-duration: 2s;
        animation-timing-function: ease; } }

@keyframes LeftToRight {
    0% {
        opacity: 0;
        transform: translateX(-100px); }
    80% {
        transform: translateX(20px); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes RightToLeft {
    0% {
        opacity: 0;
        transform: translateX(100px); }
    80% {
        transform: translateX(-20px); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }
